.container {
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding: 40px var(--leftAndRightPadding);
}
@media (max-width: 700px) {
  .container {
    padding-top: 10px;
    padding-bottom: 15px;
  }
  .container img {
    display: none;
  }
}
.container img {
  margin-left: 10px;
  height: 450px;
}
@media (max-width: 1100px) {
  .container img {
    height: 300px;
  }
}

